import { Component } from '@angular/core';
import { AuthenticationService, UserDetails } from '../authentication.service';
import { DatePipe } from '@angular/common';
import { AddExpenseComponent } from '../add-expense/add-expense.component'
import { MatDialog } from '@angular/material'

@Component({
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent {
  details: UserDetails;

  constructor(private auth: AuthenticationService, public dialog: MatDialog) { }

  ngOnInit() {
    this.auth.profile().subscribe(user => {
      const pipe = new DatePipe('en-US'); // Use your own locale
      user.expenses.forEach(expense => {
        const myFormattedDate = pipe.transform(expense.date, 'medium');
        expense.date = myFormattedDate
      });
      console.log(user)
      this.details = user;
    }, (err) => {
      console.error(err);
    });
  }
  addExpense() {
    let dialogRef = this.dialog.open(AddExpenseComponent, {
      height: '500px',
      width: '550px',
      data: this.details
    });
    dialogRef.afterClosed().subscribe(() => this.ngOnInit())
  }
}
